@import url("https://fonts.googleapis.com/css?family=Lato");

.container {
  display: block;
  position: relative;
  margin: 40px auto;
  height: auto;
  /* width: 500px; */
  padding: 20px;
}

h2 {
  color: #ffffff;
}

.container ul.question {
  list-style: none;
  margin: 0;
  padding: 0;
  /* overflow: auto; */
}

ul.question li {
  color: #aaaaaa;
  display: block;
  position: relative;
  float: left;
  width: 100%;
  /* height: 100px; */
  border-bottom: 1px solid #333;
}

ul.question li.active {
  color: #ffffff;
  display: block;
  position: relative;
  float: left;
  width: 100%;
  /* height: 100px; */
  border-bottom: 1px solid #333;
  background: #3143;
}

ul.question li input[type="radio"] {
  position: absolute;
  visibility: hidden;
}

ul.question li label {
  display: block;
  position: relative;
  font-weight: 300;
  font-size: 1.35em;
  padding: 25px 25px 25px 25px;
  margin: 10px auto;
  /* height: 30px; */
  z-index: 9;
  cursor: pointer;
  -webkit-transition: all 0.25s linear;
}

.styleNone {
  border: none !important;
}

ul.question li .check {
  display: block;
  position: absolute;
  border: 5px solid #aaaaaa;
  border-radius: 100%;
  height: 25px;
  width: 25px;
  top: 30px;
  left: 20px;
  z-index: 5;
  transition: border 0.25s linear;
  -webkit-transition: border 0.25s linear;
}

.go {
  /* width: 120px; */
  padding: 10px;
  color: black;
  font-size: 40px;
  font-weight: bold;
  margin-top: 20px;
}

ul.question li .check::before {
  display: block;
  position: absolute;
  content: "";
  border-radius: 100%;
  height: 15px;
  width: 15px;
  top: 5px;
  left: 5px;
  margin: auto;
  transition: background 0.25s linear;
  -webkit-transition: background 0.25s linear;
}

input[type="radio"]:checked ~ .check {
  border: 5px solid #0dff92;
}

input[type="radio"]:checked ~ .check::before {
  background: #0dff92;
}

input[type="radio"]:checked ~ label {
  color: #0dff92;
}

.signature {
  margin: 10px auto;
  padding: 10px 0;
  width: 100%;
}
.hide {
  display: none !important;
}

.signature p {
  text-align: center;
  font-family: Helvetica, Arial, Sans-Serif;
  font-size: 0.85em;
  color: #aaaaaa;
}

.signature .much-heart {
  display: inline-block;
  position: relative;
  margin: 0 4px;
  height: 10px;
  width: 10px;
  background: #ac1d3f;
  border-radius: 4px;
  -ms-transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

.signature .much-heart::before,
.signature .much-heart::after {
  display: block;
  content: "";
  position: absolute;
  margin: auto;
  height: 10px;
  width: 10px;
  border-radius: 5px;
  background: #ac1d3f;
  top: -4px;
}

.signature .much-heart::after {
  bottom: 0;
  top: auto;
  left: -4px;
}

.signature a {
  color: #aaaaaa;
  text-decoration: none;
  font-weight: bold;
}

/* Styles for alert... 
  by the way it is so weird when you look at your code a couple of years after you wrote it XD */

.alert {
  box-sizing: border-box;
  background-color: #bdffe1;
  width: 100%;
  position: relative;
  top: 0;
  left: 0;
  z-index: 300;
  padding: 20px 40px;
  color: #333;
}

.alert h2 {
  font-size: 22px;
  color: #232323;
  margin-top: 0;
}

.alert p {
  line-height: 1.6em;
  font-size: 18px;
}

.alert a {
  color: #232323;
  font-weight: bold;
}
